

* {
    /*Reset's every elements apperance*/
    background: none repeat scroll 0 0 transparent;
    border: medium none;
    border-spacing: 0;
    color: #26589F;
    font-family: 'PT Sans Narrow',sans-serif;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.42rem;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    text-align: left;
    text-decoration: none;
    text-indent: 0;
  }

div.splashSquaresContainer{
    margin-top: 200px;
    float: left;
    margin-left: 360px;
}

div.enterContainer{
    margin-top: 100px;

    width: 100%;
    display: inline-block;
    text-align: center;
}

div.countdownContainer{
    margin-top: 40px;

    width: 100%;
    display: inline-block;
    text-align: center;
}

div.countdownContainer div{
    display: inline-block;
}

p.countdown{
    display: inline-block;
    font-family: 'Bungee', cursive;
    color: black;
    font-size: 20px;

}

@-webkit-keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}

@keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}

button.enter{
    color: white;
    font-family: 'Bungee', cursive;
    font-size: 40px;
    padding: 20px;

    border-radius: 10px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    

    -webkit-animation: fadeIn;
    

            animation: fadeIn;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;

}

button.enter:hover{
 -webkit-box-shadow: 5px 3px 8px #CBCBCB;
         box-shadow: 5px 3px 8px #CBCBCB;
 -webkit-transition: 0.2s;
 -o-transition: 0.2s;
 transition: 0.2s
}

div.content{
    position: relative;
    top: 50%;
    -webkit-transform: translateY(50%);
        -ms-transform: translateY(50%);
            transform: translateY(50%);
    border: solid 5px;

    -webkit-box-shadow: 5px 3px 8px #CBCBCB;

            box-shadow: 5px 3px 8px #CBCBCB;
}

p.titlea{
    font-family: 'Bungee', cursive;
    font-size: 100px;
    width: 150px;
    display: inline-block;
    line-height: 100px;
    color: black;
}

.a {
    bottom:0;
    position:fixed;
    height:35px;
    margin: auto;
    width: 100%;
  }

 .page-wrap {
    text-align: left;
    width: 800px;
   margin: 0 auto
 }

 .squareContainera {
    width: 300px;
    height: 36px;
    margin: auto;

    background-color: white;
    border-top: 2px solid green;

    border-radius: 20px;

    padding-left: 10px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
 }

 div.timea{
    display: inline-block;
    text-align: center;
    overflow: visible;
    bottom:0;
    position:fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 36px;
}

div.timea p {
    line-height: 1rem;
    color: white;
    padding-top: 18px;
    padding-left: 500px;
}


