div.popupContainer{
    width: 100%;
    height: 100vh;
    text-align: center;

    -webkit-animation-name: fadeIn;

            animation-name: fadeIn;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

    opacity: 0;
}

@-webkit-keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}

@keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}

div.popupBox2{
    top: 50%;
    left: 50%;
    width: 500px;
    height: 250px;

    margin-top: -125px; /*Half of height*/
    margin-left: -250px; /*Half of width*/

    z-index: 2;
    position: absolute;

    background-color: white;
    -webkit-box-shadow: 2px 2px 10px gray;
            box-shadow: 2px 2px 10px gray;

    text-align: center;

    padding-right: 20px;
}

div.popupBox2 h1{
    color: black;
    font-size: 30px;
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;

    width: 200px;

    font-family: 'Bungee', cursive;
}

div.popupBox2 p{
    margin-left: 20px;
    color: black;

    margin-top: 40px;
    font-size: 20px;
    color: black;
    
    width: 100%;

}

button.confirm{
    color: white;
    background-color: #282828;
    border-radius: 5px;

    display: inline-block;

    padding: 5px;
    margin-top: 30px;

    -webkit-box-shadow: 5px 3px 8px #CBCBCB;

            box-shadow: 5px 3px 8px #CBCBCB;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}


