
div.adderRow{
    margin-left: 20%;
    display: inline-block;
    width: 80%;
    margin-bottom: 10px;
}


p.itemTitle{
    width: 80%;
    display: inline-block;
    text-align: left;
    margin-left: 20%;

    font-family: 'Bungee', cursive;
    font-size: 28px;
    margin-bottom: 15px;
}

div.adder{
    width: 60px;
    height: 60px;
    border-radius: 60px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;

    font-family: 'Bungee', cursive;
    color: white;
    text-align: center;
    line-height: 60px;

    -webkit-box-shadow: 5px 3px 8px #CBCBCB;

            box-shadow: 5px 3px 8px #CBCBCB;    
}

div.adderRow p {
    display: inline-block;
}

 p.owned{
    font-family: 'Bungee', cursive;
    line-height: 20px;
    font-size: 15px;

    color: black;
}


p.rate{
    font-family: 'Pacifico', cursive;
    line-height: 20px;
    font-size: 18px;

    color: black;
}


